<template>
    <div class="recharge-wrapper">
        <div v-if="loading">
            <Spin fix size="large"></Spin>
        </div>
        <h2 class="public-title">{{english.充值}}</h2>
        <div class="pay-tab clearfix">
            <a href="javascript:void(0);" :class="{'active' : onlinePay}" @click="chooseOnline"><span class="icon icon-xs"></span>{{english.线上支付}}</a>
            <a href="javascript:void(0);" :class="{'active' : !onlinePay}" @click="chooseOffline"><span class="icon icon-zx"></span>{{english.线下支付}}</a>
        </div>

        <div class="pay-info" v-if="onlinePayInfo">
            <Row type="flex" justify="space-between" class="item-list">
                <Col span="4" class="col-l">{{english.账号}}: </Col>
                <Col span="20">{{userInfo.username}}</Col>
            </Row>
            <Row type="flex" justify="space-between" class="item-list">
                <Col span="4" class="col-l">{{english.充值方式}}: </Col>
                <Col span="20">
                    <div class="recharge-list" id="onlineList">
                        <ul>
                            <li :class="{'checked' : itemIndex == 0}" @click="linkWay(0)">
                                <img src="@/assets/images/recharge/gpay.png" title="Global payment">
                            </li>

                            <li :class="{'checked' : itemIndex == 1}" @click="linkWay(1)">
                                <img src="@/assets/images/recharge/crypto.png" title="Crypto payment">
                            </li>
                        </ul>
                    </div>
                </Col>
            </Row>
            <Row type="flex" justify="space-between" class="item-list" v-show="itemIndex == 0">
                <Col span="4" class="col-l">Payer Name: </Col>
                <Col span="20">
                    <Input v-model="payerName" ref="payerName" style="width:300px"></Input>
                </Col>
            </Row>
            <Row type="flex" justify="space-between" class="item-list" v-show="itemIndex == 0">
                <Col span="4" class="col-l">Payer Email: </Col>
                <Col span="20">
                    <Input v-model="payerEmail" ref="payerEmail" style="width:300px"></Input>
                </Col>
            </Row>            
            <Row type="flex" justify="space-between" class="item-list" v-show="itemIndex == 0">
                <Col span="4" class="col-l">Payer Phone: </Col>
                <Col span="20">
                    <Input v-model="payerPhone" ref="payerPhone" style="width:300px"></Input>
                </Col>
            </Row>            
            <Row type="flex" justify="space-between" v-show="itemIndex == 0" style="padding:15px 0">
                <Col span="4" class="col-l" style="text-align:right">Payment Type: </Col>
                <Col span="9" style="text-align:left">
                    <Select type="text" placeholder="Please select" v-model="busiCode">
                        <Option value="100201">Thailand Online Banking</Option>
                        <Option value="100202">Thailand Scan Code</Option>
                        <Option value="100203">Thailand TrueMoney</Option>
                    </Select>
                </Col>
                <Col span="11" class="col-l"></Col>
            </Row>
            <Row type="flex" justify="space-between" class="item-list" v-show="itemIndex == 0 && busiCode == '100201'">
                <Col span="4" class="col-l">Bank Account: </Col>
                <Col span="20">
                    <Input v-model="bankAccount" ref="bankAccount" style="width:300px"></Input>
                </Col>
            </Row>
            <Row type="flex" justify="space-between" v-show="itemIndex == 0 && busiCode == '100201'" style="padding:15px 0">
                <Col span="4" class="col-l" style="text-align:right">Bank Code: </Col>
                <Col span="9" style="text-align:left">
                    <Select type="text" placeholder="Please select" v-model="bankCode">
                        <Option value="BAY">Bank of Ayudhya</Option>
                        <Option value="KTB">Krung Thai Bank</Option>
                        <Option value="BBL">BANGKOK BANK</Option>
                        <Option value="KBANK">Kasikornbank</Option>
                    </Select>
                </Col>
                <Col span="11" class="col-l"></Col>
            </Row>
            <Row type="flex" justify="space-between" class="item-list" v-show="itemIndex == 0">
                <Col span="4" class="col-l">{{english.充值金额}}: </Col>
                <Col span="20">
                    <div class="quick-money">
                        <span class="goods-items" :class="{'checked' : amountIndex == index}" v-for="(item, index) in quickBtn" :key="index" @click="amountIndex = index">{{item}}{{english.元}}</span><span class="goods-items other-amount" :class="{'checked' : amountIndex == -1}" @click="amountIndex = -1"><input :placeholder="english.其他" v-model="amountNum" ref="amountNum"/>{{english.元}}</span>
                    </div>
                    <Button class="next-step" :loading="btnLoading" @click="payNow">
                        <span v-if="!btnLoading">{{english.下一步}}</span>
                        <span v-else>{{english.提交中}}...</span>
                    </Button>
                </Col>
            </Row>

            <div class="taskList" v-show="itemIndex == 1">
                <a :href="cryptoPayment['hosted_url']" target="_blank" class="taskBox" v-for="(item,key) in cryptoPayment['addresses']" v-show="key=='bitcoin'||key=='ethereum'||key=='usdc'" style="margin-left:15%; width:80%" :key="key">
                    <div class="taskBoxLeft" style="width:15%">
                        <img v-if="key=='bitcoin'" :src="bitcoin" alt="" width="100%">
                        <img v-if="key=='ethereum'" :src="ethereum" alt="" width="100%">
                        <img v-if="key=='usdc'" :src="usdc" alt="" width="100%">
                    </div>
                    <div class="taskBoxText" style="margin-left:5%; width:65%">
                        <h4>{{key[0].toUpperCase() + key.substring(1)}}: {{item}}</h4>
                        <p v-if="key=='bitcoin'">BTC-USD: {{cryptoPayment['exchange_rates']['BTC-USD']}}</p>
                        <p v-if="key=='ethereum'">ETH-USD: {{cryptoPayment['exchange_rates']['ETH-USD']}}</p>
                        <p v-if="key=='usdc'">USDC-USD: {{cryptoPayment['exchange_rates']['USDC-USD']}}</p>
                    </div>
                    <div class="taskBoxRight" style="width:15%">
                        <img :src="codeImg" width="100%" alt="">
                    </div>
                </a>
            </div>
        </div>

        <div class="pay-info" v-if="offlinePayInfo && this.offlineList.length">
            <Row type="flex" justify="space-between" class="item-list">
                <Col span="4" class="col-l">{{english.账号}}: </Col>
                <Col span="20">{{userInfo.username}}</Col>
            </Row>
            <Row type="flex" justify="space-between" class="item-list">
                <Col span="4" class="col-l">{{english.充值方式}}: </Col>
                <Col span="20">
                    <div class="recharge-list" id="offlineList">
                        <ul>
                            <li :class="{'checked' : itemIndex == index}" v-for="(item,index) in offlineList" :key="index" @click="linkFun(item, index, 1)">
                                <template v-if="item.handsel > 0">
                                    <Tooltip placement="top">
                                        <img :src="imgRequest + item.logo">
                                        <span>{{item.payment_name}}</span>
                                        <div slot="content">{{english.返现比例}}: {{item.handsel}}%</div>
                                    </Tooltip>
                                </template>
                                <template v-else>
                                    <img :src="imgRequest + item.logo">
                                    <span>{{item.payment_name}}</span>
                                </template>
                            </li>
                            <!-- <li :class="{'checked' : itemIndex == index}" v-for="(item,index) in offlineList" :key="index" @click="linkFun(item, index, 1)">
                                <img :src="imgRequest + item.logo">
                                <span>{{item.payment_name}}</span>
                            </li> -->
                        </ul>
                    </div>
                </Col>
            </Row>
            <Row type="flex" justify="space-between" class="item-list">
                <Col span="4" class="col-l">{{english.充值金额}}: </Col>
                <Col span="20">
                    <div class="quick-money">
                        <span class="goods-items" :class="{'checked' : amountIndex == index}" v-for="(item, index) in quickBtn" :key="index" @click="amountIndex = index">{{item}}{{english.元}}</span><span class="goods-items other-amount" :class="{'checked' : amountIndex == -1}" @click="amountIndex = -1"><input :placeholder="english.其他" v-model="amountNum" ref="amountNum"/>{{english.元}}</span>
                    </div>
                    <p class="recharge-tips text-gray" v-show="rechargeData.min_recharge && rechargeData.max_recharge">
                        <em class="text-red">*</em>{{english.充值限额}}: 
                        <span v-if="rechargeData.max_recharge == -1">{{english.该方式额度已达充值限额}}</span>
                        <span v-else>{{rechargeData.min_recharge}} ~ {{rechargeData.max_recharge}}, {{english.建议通过银行卡转账入款}}</span>
                    </p>
                    <Button class="next-step" :loading="btnLoading" :disabled="rechargeData.max_recharge == -1" @click="payNow">
                        <span v-if="!btnLoading">{{english.下一步}}</span>
                        <span v-else>{{english.提交中}}...</span>
                    </Button>
                </Col>
            </Row>
            <div class="tips" v-show="rechargeData.prompt != ''">
                <h4>{{english.温馨提示}}</h4>
                <p v-html="rechargeData.prompt.replace(/\\n/g,'</br>')"></p>
            </div>
        </div>

        <div class="pay-info pay-info-online" v-if="onlinePayTwo">
            <div class="row-wrap">
                <Row type="flex" justify="center" align="middle">
                    <Col span="12">
                        <Row type="flex" justify="space-between" class="row-list">
                            <Col span="8" class="col-l">Payment Method: </Col>
                            <Col span="16">{{onlinePaymentMethod}}</Col>
                            <!-- <Col span="16">{{issuingBank}}</Col> -->
                        </Row>
                        <Row type="flex" justify="space-between" class="row-list">
                            <Col span="8" class="col-l">{{english.订单号}}: </Col>
                            <Col span="16">{{order_no}}</Col>
                        </Row>
                        <Row type="flex" justify="space-between" class="row-list">
                            <Col span="8" class="col-l">{{english.充值金额}}: </Col>
                            <Col span="16">{{amountVal}} {{english.元}}</Col>
                        </Row>
                    </Col>
                    <Col span="12" class="col-r">
                        <img v-if="codeImg && codeImg != ''" :src="codeImg" alt="">
                        <span v-else id="qrcode" ref="qrcode"></span>
                        <p>{{english.扫一扫二维码支付}}</p>
                    </Col>
                </Row>
            </div>
            <div class="btn-wrap">
                <Button class="next-step" :loading="btnLoading" @click="submitOnLine">
                    <span v-if="!btnLoading">{{english.我已完成支付}}</span>
                    <span v-else>{{english.提交中}}...</span>
                </Button>
            </div>
        </div>

        <div class="pay-info pay-info-offline" v-if="offlinePayTwo">
            <div class="row-wrap">
                <Row type="flex" justify="center" align="middle">
                    <Col span="12">
                        <Row type="flex" justify="space-between" class="row-list">
                            <Col span="12" class="col-l">{{english.发卡银行}}: </Col>
                            <Col span="12">{{rechargeData.name}}<span class="icoCopy" :data-clipboard-text="rechargeData.name" @click="copyText"><Icon type="document"></Icon></span></Col>
                        </Row>
                        <Row type="flex" justify="space-between" class="row-list">
                            <Col span="12" class="col-l">{{english.收款人}}: </Col>
                            <Col span="12">{{rechargeData.account_name}}<span class="icoCopy" :data-clipboard-text="rechargeData.account_name" @click="copyText"><Icon type="document"></Icon></span></Col>
                        </Row>
                        <Row type="flex" justify="space-between" class="row-list">
                            <Col span="12" class="col-l">{{english.收款账号}}: </Col>
                            <Col span="12">{{rechargeData.account}}<span class="icoCopy" :data-clipboard-text="rechargeData.account" @click="copyText"><Icon type="document"></Icon></span></Col>
                        </Row>
                        <Row type="flex" justify="space-between" class="row-list">
                            <Col span="12" class="col-l">Branch Address: </Col>
                            <Col span="12">{{rechargeData.branch}}<span class="icoCopy" :data-clipboard-text="rechargeData.branch" @click="copyText"><Icon type="document"></Icon></span></Col>
                        </Row>
                        <Row type="flex" justify="space-between" class="row-list">
                            <Col span="12" class="col-l">{{english.附加码}}: </Col>
                            <Col span="12">{{code}}<span class="icoCopy" :data-clipboard-text="code" @click="copyText"><Icon type="document"></Icon></span></Col>
                        </Row>
                        <Row type="flex" justify="space-between" class="row-list">
                            <Col span="12" class="col-l">{{english.充值金额}}: </Col>
                            <Col span="12">{{amountVal}} {{english.元}}</Col>
                        </Row>
                    </Col>
                    <Col span="12" class="col-r">
                        <template v-if="rechargeData.code">
                            <img :src="imgRequest + '/' + rechargeData.code" alt="">
                            <p>{{rechargeData.name}} {{english.扫码支付}}</p>
                        </template>
                        <template v-else>
                            <img :class="{'border-none' : !rechargeData.code}" :src="imgRequest + rechargeData.logo" alt="">
                            <p>{{rechargeData.name}} {{english.支付}}</p>
                        </template>
                    </Col>
                </Row>
            </div>
            <div class="btn-wrap">
                <Button class="next-step" :loading="btnLoading" @click="submitOffLine">
                    <span v-if="!btnLoading">{{english.点击确认提交}}</span>
                    <span v-else>{{english.提交中}}...</span>
                </Button>
            </div>
        </div>

        <div class="pay-info pay-status" v-if="successFlag">
            <div class="row-wrap">
                <img src="@/assets/images/common_success_pic@2x.png" alt="">
                <p>{{english.支付信息提交成功}}</p>
                <div class="mtb-min">
                    <Row type="flex" justify="space-between" class="row-list">
                        <Col span="12" class="col-l">{{english.充值方式}}:  </Col>
                        <Col span="12" class="col-r">{{this.onlinePay ? english.线上 : english.线下}} {{onlinePaymentMethod!=''?onlinePaymentMethod:rechargeData.name}} {{english.转账}}</Col>
                    </Row>
                    <Row type="flex" justify="space-between" class="row-list">
                        <Col span="12" class="col-l">{{english.充值金额}}: </Col>
                        <Col span="12" class="col-r">{{amountVal}} {{english.元}}</Col>
                    </Row>
                    <Row type="flex" justify="space-between" class="row-list" v-show="!this.onlinePay">
                        <Col span="12" class="col-l">{{english.附加码}}: </Col>
                        <Col span="12" class="col-r">{{code}}</Col>
                    </Row>
                </div>
            </div>
            <div class="btn-wrap"><a href="javascript:void(0);" class="next-step" @click="successBtn">{{english.返回}}</a></div>
        </div>
        <div class="pay-info pay-status" v-if="errorFlag">
            <div class="row-wrap">
                <img src="@/assets/images/common_fault_pic@2x.png" alt="">
                <p>{{english.充值异常}}</p>
                <div class="mtb-min">
                    <Row type="flex" justify="space-between" class="row-list">
                        <Col span="12" class="col-l">{{english.充值方式}}: </Col>
                        <Col span="12" class="col-r">{{onlinePaymentMethod!=''?onlinePaymentMethod:rechargeData.payment_name}}</Col>
                    </Row>
                    <Row type="flex" justify="space-between" class="row-list">
                        <Col span="12" class="col-l">{{english.充值金额}}: </Col>
                        <Col span="12" class="col-r">{{amountVal}} {{english.元}}</Col>
                    </Row>
                </div>
            </div>
            <div class="btn-wrap"><a href="javascript:void(0);" class="next-step" @click="errorBtn">{{english.重新充值}}</a></div>
        </div>

        <Modal
            v-model="modalPayAfter"
            width="400"
            :title="english.温馨提示"
            :okText='english.支付成功'
            :cancelText='english.未支付'
            @on-ok="ModalOk"
            @on-cancel="ModalCancel"
        >
            <div style="font-size:14px;">{{english.请根据支付情况点击下方按钮请不要重复支付}}</div>
        </Modal>

    </div>
</template>
<script>
import { jsonToString } from "@/common/js/jsonToString";
import QRCode from 'qrcodejs2';
import bitcoin from "@/assets/images/recharge/bitcoin.png";
import ethereum from "@/assets/images/recharge/ethereum.png";
import usdc from "@/assets/images/recharge/usdc.png";
import Clipboard from 'clipboard'
import { mapGetters, mapActions } from "vuex";
import english from "@/assets/json/English.json";

export default {
    data () {
        return {
            bitcoin: bitcoin,
            ethereum: ethereum,
            usdc: usdc,
            english: english,
            onlinePay: true, //默认线上支付
            onlineList: '',
            offlineList: '',
            quickBtn: {}, //推荐充值金额

            onlinePayInfo: true, //线上充值信息            
            onlinePayTwo: false,

            offlinePayInfo: false, //充值信息
            offlinePayTwo: false,

            successFlag: false, //充值成功
            errorFlag: false, //充值异常
            rechargeData: {},  //充值数据
            itemIndex: 0,
            onlinePaymentMethod: "",
            cryptoPayment: {},
            payerName: "",
            payerEmail: "",
            payerPhone: "",
            busiCode: "",
            bankAccount: "",
            bankCode: "",
            amountIndex: 0,
            amountNum: '',
            amountVal: null,
            code: '',
            order: '',
            codeUrl: '',
            codeImg: '',
            paymentLink: '',

            bankList: [], //充值银行列表
            selectedbankcode: "", //充值银行的bank_code

            loading: false,
            btnLoading: false,
            // issuingBank: '',

            modalPayAfter: false,

            onlineHandsel: '', //线上支付返现比列
        }
    },
    created() {
        
    },
    mounted() {
        this.amountNum = '';
        this._getRechargeList();
    },
    computed: {
        ...mapGetters(['userInfo','accountData'])
    },
    methods: {
        linkWay(index){
            if(index==1){
                this.$http.post(this.urlRequest + "?m=api&c=recharge&a=createCharge").then(res => {
                    console.log(res);
                    if(res.data.status==0){
                        this.cryptoPayment = res.data.data;                        
                        this.itemIndex = index;
                        this.amountIndex = 0;
                        this.amountNum = '';
                    }
                })
            }else{
                this.itemIndex = index;
                this.amountIndex = 0;
                this.amountNum = '';
            }
        },
        linkFun(item, index, line) {
            this.rechargeData = item;
            this.amountIndex = 0;
            this.itemIndex = index;
            this.amountNum = '';
            if (this.rechargeData.type_mode == 2) {
                this._getBankList(); //获取充值银行列表
            }
            // console.log('item', item);
        },
        _getRechargeList() {
            if (!JSON.parse(localStorage.getItem("isUser"))) {
                this.onlineList = [];
                this.offlineList = [];
                this.$Message.info(english.游客暂无权限+', '+english.请先注册会员)
				return
            }
            var params = {
                token: localStorage.getItem("token")
            };
            this.loading = true;
            this.$http.post(this.urlRequest + "?m=api&c=recharge&a=offlineIndex", params).then(res => {
                this.loading = false;
                if (res.data.status == 0) {
                    this.onlineList = res.data.list2;
                    this.offlineList = res.data.list;
                    this.onlineHandsel = res.data.online_handsel;
                    this.quickBtn = res.data.quick_btn;
                    if (this.onlinePay) {
                        this.rechargeData = this.onlineList[0];
                    } else {
                        this.rechargeData = this.offlineList[0];
                    }
                }
            }).catch(error => {
                this.loading = false;
				this.$Message.warning(english.数据请求超时);
			});
        },
        _getBankList() { //获取充值银行列表
            var params = {
                token: localStorage.getItem("token"),
                payment_id: this.rechargeData.payment_id
            };
            // this.$vux.loading.show();
            this.$http.post(this.urlRequest + "?m=api&c=recharge&a=getRechargeBankList", params).then(res => {
                // this.$vux.loading.hide();
                if (res.data.status == 0) { //银行列表
                    this.bankList = res.data.bankList;
                    this.selectedbankcode = this.bankList[0].bank_code; //获取银行简码
                } else {
                    if (res.data.ret_msg !== '') {
                        this.$Message.warning(res.data.ret_msg);
                    }
                }
            }).catch(error => {
                // this.$vux.loading.hide();
                this.$Message.warning(english.获取银行列表失败);
            });
        },
        
        chooseOnline() { //线上充值
            // clearInterval(this.timer);
            if (this.onlinePayTwo || this.offlinePayTwo) {
                this.$Modal.confirm({
                    title: english.温馨提示,
                    content: english.切换后将取消当前未完成订单+", "+english.是否继续+"?",
                    okText: english.确定,
                    cancelText: english.取消,
                    onOk: () => {
                        this.onlineInfo()
                    }
                });
            } else {
                this.onlineInfo()
            }
        },
        onlineInfo() { //线上充值数据展示处理
            this.rechargeData = this.onlineList[0];
            this.onlinePay = true;
            this.onlinePayInfo = true;
            this.onlinePayTwo = false;
            this.itemIndex = 0; //默认选中第一个支付方式
            this.amountIndex = 0; //默认选中第一个金额
            this.amountNum = ''; //其他金额清空
            this.offlinePayInfo = false;
            this.offlinePayTwo = false;
            this.successFlag = false;
            this.errorFlag = false;
        },
        chooseOffline() { //线下充值
            // clearInterval(this.timer);
            if (this.onlinePayTwo || this.offlinePayTwo) {
                this.$Modal.confirm({
                title: english.温馨提示,
                content: english.切换后将取消当前未完成订单+", "+english.是否继续+"?",
                okText: english.确定,
                cancelText: english.取消,
                onOk: () => {
                    this.offlineInfo()
                }
                });
            } else {
                this.offlineInfo()
            }
        },
        offlineInfo() { //线下充值数据展示处理
            this.rechargeData = this.offlineList[0];
            this.onlinePay = false;
            this.offlinePayInfo = true;
            this.offlinePayTwo = false;
            this.itemIndex = 0; //默认选中第一个支付方式
            this.amountIndex = 0; //默认选中第一个金额
            this.amountNum = ''; //其他金额清空
            this.onlinePayInfo = false;
            this.onlinePayTwo = false;
            this.successFlag = false;
            this.errorFlag = false;
        },
        payNow() {
            if (this.amountIndex == -1) {
                this.amountVal = this.amountNum
            } else {
                this.amountVal = this.quickBtn[this.amountIndex];
            }
            var newAmountVal = Number(this.amountVal);
            
            if (this.amountVal == '' || this.amountVal == 0) {
                this.$refs.amountNum.focus();
                this.$Message.warning(english.请输入充值金额);
                return
            }
            var formatAmount = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
            if (!formatAmount.test(this.amountVal)) {
                this.$refs.amountNum.focus();
                this.$Message.warning(english.输入的资金格式有误)
				return
            }

            if (this.onlinePay) { //线上
                
                if (this.itemIndex == 0) {
                    var params = {
                        pname: this.payerName,
                        pemail: this.payerEmail,
                        phone: this.payerPhone,
                        order_amount : this.amountVal ? this.amountVal: "",
                        busi_code: this.busiCode,
                        accNo: this.bankAccount,
                        bankCode: this.bankCode
                    };

                    //get_hash
                    this.$http.post(this.urlRequest + "?m=api&c=recharge&a=globalPay", params).then(res => {
                            res = res.data;
                            this.btnLoading = false;
                            this.loading = false;

                            if (res.status == 'SUCCESS') {
                                window.location.href=res.order_data;
                                this.onlinePayInfo = false;
                                this.onlinePayTwo = true;
                                this.order_no = res.order_no;
                                this.onlinePaymentMethod = "Gpay payment";                                
                            } else {
                                this.onlinePayInfo = false;
                                this.onlinePaymentMethod = "Gpay payment";
                                this.errorFlag = true;
                                if (res.err_msg !== '') {
                                    this.$Message.warning(res.err_msg);
                                }
                            }
                        })
                    // });                    
                }
            } else { //线下
                var newMinRecharge = Number(this.rechargeData.min_recharge);
                var newMaxRecharge = Number(this.rechargeData.max_recharge);
                if (newAmountVal < newMinRecharge) {
                    this.$Message.warning(english.充值金额不能小于 + newMinRecharge + english.元)
                    return
                }
                if (newAmountVal > newMaxRecharge) {
                    this.$Message.warning(english.充值金额不能大于 + newMaxRecharge + english.元)
                    return
                }
                var params = {
                    token: localStorage.getItem("token"),
                    id: this.rechargeData.payment_id,
                    money : this.amountVal ? this.amountVal : ''
                };
                this.btnLoading = true;
                this.loading = true;
                this.$http.post(this.urlRequest + "?m=api&c=recharge&a=rechargeOffline", params).then(res => {
                    this.btnLoading = false;
                    this.loading = false;
                    if (res.data.status == 0) {
                        this.order = res.data.order_sn;
                        this.code = res.data.code;
                        this.offlinePayInfo = false;
                        this.offlinePayTwo = true;
                    } else {
                        this.$Message.warning(res.data.ret_msg);
                    }
                }).catch(error => {
                    this.btnLoading = false;
                    this.loading = false;
                    this.$Message.warning(english.数据请求超时);
                });
            }
        },
        qrcode(Url) {
            let qrcode = new QRCode('qrcode', {  
                width: 150,  // 设置宽度 
                height: 150, // 设置高度
                text: url
            })  
        },
        ModalPayOk() {
            window.open(this.paymentLink)
            this.modalPayAfter = true;
        },
        ModalOk() {
            this.modalPayAfter = false;
            this.onlinePayInfo = false;
            this.successFlag = true;
        },
        ModalCancel() {
            this.modalPayAfter = false;
            this.onlinePayInfo = false;
            this.errorFlag = true;
        },
        copyText() {
            var clipboard = new Clipboard('.icoCopy')
            clipboard.on('success', e => {
                this.$Message.success(english.复制成功);
                clipboard.destroy() // 释放内存
            })  
            clipboard.on('error', e => {
                this.$Message.warning(english.该浏览器不支持自动复制);
                clipboard.destroy() // 释放内存
            })
        },
        submitOnLine() { //线上已完成支付
            var params = {
                token: localStorage.getItem("token"),
                order_no: this.order_no
            };
            this.btnLoading = true;
            // clearInterval(this.timer);
            this.$http.post(this.urlRequest + "?m=api&c=recharge&a=getRechargeInfo", params).then(res => {
                this.btnLoading = false;
                if (res.data.status == 0) {
                    setTimeout(()=> {
                        if (res.data.state == 0) {
                            this.payerName = "";
                            this.payerEmail = "";
                            this.payerPhone = "";
                            // this.timer = setInterval(() => {
                            //     this.submitOnLine();
                            // }, 3000)
                            return
                        }
                        if (res.data.state == 1) {
                            this.onlinePayTwo = false;
                            this.successFlag = true;
                        }
                    }, 3000)
                }
            }).catch(error => {
                // this.timer = setInterval(() => {
                //     this.submitOnLine();
                // }, 3000)
                this.btnLoading = false;
                this.$Message.error(english.数据请求超时);
            });
        },
        submitOffLine() { //线下点击确认提交
            var params = {
                token: localStorage.getItem("token"),
                order_sn: this.order
            };
            this.btnLoading = true;
            this.$http.post(this.urlRequest + "?m=api&c=recharge&a=setRechargeMusic", params).then(res => {
                this.btnLoading = false;
                if (res.data.status == 0) {
                    this.offlinePayTwo = false;
                    this.successFlag = true;
                } else {
                    this.offlinePayTwo = false;
                    this.errorFlag = true;
                    this.$Message.error(res.data.ret_msg);
                }
            }).catch(error => {
                this.btnLoading = false;
                this.$Message.error(english.数据请求超时);
            });
        },
        successBtn() { //充值成功
            this.$Modal.success({
                content: english.已完成支付+", "+english.确认关闭+"?",
                onOk: () => {
                    this.onlinePay = true;
                    this.onlinePayInfo = true;
                    this.successFlag = false;
                }
            });
        },
        errorBtn() { //充值异常
            this.onlinePay = true;
            this.onlinePayInfo = true;
            this.errorFlag = false;
        },
    },
    watch: {
        chargeData:function(data){
            this.cryptoPayment = data;
        },     
        amountNum:function(newVal, oldVal) {
            if (isNaN(newVal)) {
                this.amountNum = oldVal;
            } else {
                var re = /([0-9]+\.[0-9]{2})[0-9]*/;
                this.amountNum = newVal.replace(re, "$1");
            }            
        },
        onlinePay(bol) {
            if (this.onlineList.length || this.offlineList.length) {
                if (bol) {
                    this.rechargeData = this.onlineList[0];
                } else {
                    this.rechargeData = this.offlineList[0];
                }
            }
        },
    },
    destroyed() {
        // clearInterval(this.timer);
    }
}
</script>
<style scoped>
.recharge-wrapper {
    position: relative;
}
.pay-tab {
    margin-top: 20px;
    margin-bottom: 10px;
    border-bottom: 1px solid #eee
}
.pay-tab a {
    float: left;
    margin-bottom: -1px;
    width: 200px;
    height: 36px;
    text-align: center;
    line-height: 36px;
