    background-position: -226px -302px
}
.pay-tab a.active .icon-xs {
    background-position: -250px -284px
}
.pay-tab a.active .icon-zx {
    background-position: -250px -302px
}



.item-list {
    padding: 15px 0;
}
.row-list {
    padding: 5px 0;
    font-size: 15px;
}
.item-list .col-l,
.row-list .col-l {
    text-align: right;
    padding-right: 5px;
}
.row-list .col-r {
    text-align: left;
    padding-left: 5px;
}
.recharge-list ul {
    margin-top: -15px;
}
.recharge-list ul li {
    position: relative;
    display: inline-block;
    margin-right: 20px;
    margin-top: 15px;
	height: 40px;
	line-height: 20px;
    cursor: pointer;
    background: #fff;
	border: 1px solid #ccc;
}
#onlineList ul li {
    padding: 0;
}
#offlineList ul li {
    padding: 10px;
}
.recharge-list ul li.checked,
.recharge-list ul li:hover {
	border-color: #F3596A;
}
.recharge-list ul li img {
	float: left;
	margin-right: 8px;	
}
#onlineList ul li img {
    height:100%;
}
#offlineList ul li img {
    height: 20px;
    width: 20px
}
.quick-money {
    margin-top: -15px;
    width: 440px;
}
.quick-money .goods-items {
    position: relative;
    display: inline-block;
    margin-right: 20px;
    margin-top: 15px;
    text-align: center;
    width: 90px;
    height: 40px;
    line-height: 40px;
    cursor: pointer;
    background: #fff;
    border: 1px solid #ccc;
}
.quick-money .goods-items:hover,
.quick-money .goods-items.checked {
    border-color: #F3596A;
}
.recharge-list ul li.checked::after,
.quick-money .goods-items.checked::after {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    width: 10px;
    height: 10px;
    /* background: url('../../../assets/images/recharge/checked.png') no-repeat; */
}
.quick-money .other-amount {
    width: 200px;
}
.quick-money .other-amount input {
    float: left;
    margin: 4px 0 0 10px;
    width: 150px;
    padding: 0 4px;
    height: 30px;
    line-height: 28px;
    border: 1px solid #ccc;
}
.quick-money .other-amount input:focus {
    border-color: #57a3f3;
} 
.next-step,
.next-step:hover {
    display: inline-block;
    margin-top: 30px;
    padding: 0 20px;
    min-width: 90px;
    height:30px;
    line-height: 30px;
    color: #fff;
    background:rgba(243,89,106,1);
    border: 0;
    outline: none;
    border-radius:2px;
}
.next-step[disabled],
.next-step[disabled]:hover {
