}
.pay-info .row-wrap {
    margin: 50px 0;
}
.pay-info-online .col-r,
.pay-info-offline .col-r {
    text-align: center;
    border-left: 1px solid #eee;
}
.pay-info-online .col-r #qrcode,
.pay-info-offline .col-r img {
    display: inline-block;
    width: 150px;
    height: 150px;
    border: solid 1px #979797;
}
.pay-info-offline .col-r .border-none {
